import Link from 'next/link'


const MainContents_Left_Style = {
    display : 'flex',
    float : 'left',
    flexDirection : 'column',
    alignItems : 'left',
    height : '100%',
    minHeight : 'calc(100vh - 100px)',
    width : '49%',
    border : 'groove 1px',
}

const Title_Left_Style = {
  textAlign : 'center',
  padding : '15px 0px',
  marginBottom : '20px',
  backgroundColor : '#999',
}

const Content_Left_Style = {
  marginTop : '20px',
  marginLeft : '10px',
  border : 'groove 1px',
}


function MainContents_Left(){
  return(
    <div style={MainContents_Left_Style}>

      <style jsx>{`
      .today:hover {
        background-color : #999;
      }

      .today {
        font-size : 20px;
        text-decoration : none;
        color : black;
        background-color : #f7f7f7;
        border-radius : 2px;
        padding : 3px;
        border : solid 1px;
        margin : 0px 20px;
      }

      .sevendays:hover {
        background-color : #999;
      }

      .sevendays {
        font-size : 20px;
        text-decoration : none;
        color : black;
        background-color : #f7f7f7;
        border-radius : 2px;
        padding : 3px;
        border : solid 1px;
        margin : 0px 20px;
      }

      .addTodo {
        text-decoration : none;
        color : black;
        
      }

      .addTodo:hover {

      }
      `}</style>

      <div className="title_left" style={Title_Left_Style}>
        일상
      </div>

      <div>
        <Link href="/">
          <a className="today">오늘</a>
        </Link>
        <Link href="/">
          <a className="sevendays">앞으로의 7일</a>
        </Link>
      </div>

      <Link href="/">
        <a className="addTodo"><strong>+ 할 일 추가</strong></a>
      </Link>

      <div className="content_left" style={Content_Left_Style}>
      </div>

    </div>
  )
}


export default MainContents_Left
